  }
}
.a11y-tip__trigger[aria-describedby].a11y-tip__trigger--toggle:hover + .a11y-tip__help,
.a11y-tip__trigger[aria-describedby].a11y-tip__trigger--toggle:hover + .a11y-tip__help:after {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}
.a11y-tip__trigger[aria-expanded=true].a11y-tip__trigger--toggle:hover + .a11y-tip__help,
.a11y-tip__trigger[aria-expanded=true].a11y-tip__trigger--toggle:hover + .a11y-tip__help:after {
  opacity: 1 !important;
  pointer-events: auto !important;
  -webkit-transition: opacity 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
  transition: opacity 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
  transition: opacity 0.18s ease-in-out, transform 0.18s ease-in-out;
  transition: opacity 0.18s ease-in-out, transform 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
  visibility: visible !important;
}
.a11y-tip--no-delay .a11y-tip__help,
.a11y-tip--no-delay .a11y-tip__help:after {
  -webkit-transition: opacity 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
  transition: opacity 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
  transition: opacity 0.18s ease-in-out, transform 0.18s ease-in-out;
  transition: opacity 0.18s ease-in-out, transform 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
}
[role=tooltip].a11y-tip__help {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 4px;
  color: #fff;
  font-size: 14px;
  left: 50%;
  margin-top: 12px;
  max-width: 400px;
  min-width: 220px;
  opacity: 0;
  padding: 0.5em 1em;
  pointer-events: none;
  position: absolute;
  text-align: left;
  top: 100%;
  -webkit-transform: translate(-50%, 10px);
          transform: translate(-50%, 10px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: opacity 0.2s 1s ease-in-out;
  transition: opacity 0.2s 1s ease-in-out;
  visibility: hidden;
  width: 100%;
  z-index: 10;
}
[role=tooltip].a11y-tip__help:after {
  border: inset 6px;
  content: "";
  display: inline-block;
  height: 0;
  speak: none;
  width: 0;
  border-color: transparent transparent rgba(0, 0, 0, 0.9) transparent;
  border-bottom-style: solid;
  bottom: 100%;
  content: "";
  left: 50%;
  margin-top: 6px;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  -webkit-transform: translate(-50%, 10px);
          transform: translate(-50%, 10px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: opacity 0.2s 1s ease-in-out;
  transition: opacity 0.2s 1s ease-in-out;
  visibility: hidden;
  z-index: 10;
}
[role=tooltip].a11y-tip__help--top {
  bottom: 100%;
  left: 50%;
  margin-bottom: 12px;
  margin-top: 0;
  top: auto;
  -webkit-transform-origin: center top;
          transform-origin: center top;
  -webkit-transform: translate(-50%, 10px);
          transform: translate(-50%, 10px);
}
[role=tooltip].a11y-tip__help--top:after {
  border: inset 6px;
  content: "";
  display: inline-block;
  height: 0;
  speak: none;
  width: 0;
  border-color: rgba(0, 0, 0, 0.9) transparent transparent transparent;
  border-top-style: solid;
  left: 50%;
  margin-bottom: 6px;
  margin-top: 0;
  top: 100%;
  -webkit-transform-origin: center top;
          transform-origin: center top;
  -webkit-transform: translate(-50%, -10px);
          transform: translate(-50%, -10px);
}
@media screen and (min-width: 40.625em) {

  [role=tooltip].a11y-tip__help--left {
    left: auto;
    margin-right: 11px;
    margin-top: 0;
    right: 100%;
    top: 50%;
    -webkit-transform: translate(10px, -50%);
            transform: translate(10px, -50%);
  }

  [role=tooltip].a11y-tip__help--left:after {
    border: inset 6px;
    content: "";
    display: inline-block;
    height: 0;
    speak: none;
    width: 0;
    border-color: transparent transparent transparent rgba(0, 0, 0, 0.9);
    border-left-style: solid;
    left: 100%;
    margin-right: 5px;
    margin-top: 0;
    top: 50%;
    -webkit-transform: translate(-10px, -50%);
            transform: translate(-10px, -50%);
  }

  [role=tooltip].a11y-tip__help--right {
    left: 100%;
    margin-left: 11px;
    margin-top: 0;
    top: 50%;
    -webkit-transform: translate(10px, -50%);
            transform: translate(10px, -50%);
  }

  [role=tooltip].a11y-tip__help--right:after {
    border: inset 6px;
    content: "";
    display: inline-block;
    height: 0;
    speak: none;
    width: 0;
    border-color: transparent rgba(0, 0, 0, 0.9) transparent transparent;
    border-right-style: solid;
    bottom: auto;
    left: auto;
    margin-left: 5px;
    margin-top: 0;
    right: 100%;
    top: 50%;
    -webkit-transform: translate(10px, -50%);
            transform: translate(10px, -50%);
  }
}
.a11y-tip--hide ~ .a11y-tip__help {
  display: none;
}
.no-js .a11y-tip__help:not([role]) {
		font-size: 0.85em
	}
.no-js .a11y-tip__help:not([role]):before {
			content: "(";
		}
.no-js .a11y-tip__help:not([role]):after {
			content: ")";
		}
